@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    box-sizing: border-box;
    color: white;
    font-family: Lato, sans-serif;
}

:root {
    --dark-orange: #e3890f;
    --light-orange: #ff9c17;
}

.App {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, .3)), to(rgba(0, 0, 0, .2))), url(/reactFlashCards/static/media/joao-silas-65013-unsplash.bc847bd0.jpg);
    background: -webkit-linear-gradient(rgba(0, 0, 0, .3), rgba(0, 0, 0, .2)), url(/reactFlashCards/static/media/joao-silas-65013-unsplash.bc847bd0.jpg);
    background: linear-gradient(rgba(0, 0, 0, .3), rgba(0, 0, 0, .2)), url(/reactFlashCards/static/media/joao-silas-65013-unsplash.bc847bd0.jpg);
    background-size: cover;
    background-position: center;
    height: 100vh;
    width: 100vw;
}

.flashCard {
    background-color: rgba(255, 255, 255, .95);
    /*max-width: 65vw; */
    /*max-height: 40vh; */
    font-size: 5vh;
    box-shadow: 0 0 0 2px rgba(255, 255, 255, .07);
    border: 5px solid #ff9c17;
    border: 5px solid var(--light-orange);
    border-radius: 10px;
    padding: 2px 10px;
    color: black;
    margin-bottom: 5vh;
}

.flashCard:hover {
    cursor: pointer;
}

.btn-topics {
    font-size: 3.5vh;
    border-radius: 20px;
    padding: 1vh 1vw;
    border: 2px solid #ff9c17;
    border: 2px solid var(--light-orange); 
    display: inline;
    margin: 10px;
    -webkit-transition: background-color .5s, border .2s, -webkit-transform .3s;
    transition: background-color .5s, border .2s, -webkit-transform .3s;
    transition: background-color .5s, border .2s, transform .3s;
    transition: background-color .5s, border .2s, transform .3s, -webkit-transform .3s; 
    max-height: 6vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn-topics:hover {
    cursor: pointer;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.btn-topics-selected {
    background-color: #e3890f;
    background-color: var(--dark-orange);
    border: 2px solid #e3890f;
    border: 2px solid var(--dark-orange);
}

i {
    padding-left: 5px;
}

i:hover {
    cursor: pointer;
    color: black;
}

a:hover {
    color: green;
    text-decoration: none;
}

.button-sml {
    background-color: #ff9c17;
    background-color: var(--light-orange);
    border-radius: 30px;
    padding: 5px 15px;
    margin: 0px 10px;
    font-size: 3.5vh;
    -webkit-transition: all .5s;
    transition: all .5s;
}

.button-sml:hover {
    cursor: pointer;
    background-color: #e3890f;
    background-color: var(--dark-orange);
    -webkit-transform: scale(1.06);
            transform: scale(1.06);
}

.button-github {
    border-radius: 3px; 
    padding: 2px;
    background: gray;
    border: 1px;
    border-style: solid;
    border-color: rgb(216, 216, 216) rgb(209, 209, 209) rgb(186, 186, 186);
}

span {
    color: black;
}

.row1 {
    height: 25vh;
}

.row2 {
    min-height: 45vh;
    display: flex;
    justify-content: center;
}

.row3 {
    display: flex; 
    align-items: center;
    justify-content: center;
}

.row4 {
    display: flex;
    justify-content: center;
    margin-top: 30px;
}


.btn-default {
    border-radius: 3px;
    padding: 2px;
    color: #333;
    background-color: #fff;
    border-color: #ccc;
}

.btn-default:hover {
    color: #333;
    background-color: #e6e6e6;
    border-color: #adadad;
}



